.featured{
    width: 100%;
    display: flex;
    justify-content: space-between;
}
.featuredItem{
    flex: 1;
    margin: 0px 20px;
    padding: 30px;
    cursor: pointer;
    border-radius: 10px;
    -webkit-box-shadow: 0px 0px 15px -10px rgba(0,0,0,0.75); 
    box-shadow: 0px 0px 15px -10px rgba(0,0,0,0.75);
}
.featuredTitle{
    font-size: 22px;
}
.featurdMoneyContainer{
    margin: 10px 0px;
    display: flex;
    align-items: center;
}
.featuredMoney{
    font-size: 30px;
    font-weight: 600;
}
.featuredMoneyRate{
    display: flex;
    align-items: center;
    margin-left: 15px;
}
.featuredIconPos{
    font-size: 14px;
    margin-left: 5px;
    color: green;
}
.featuredIconNeg{
    font-size: 14px;
    margin-left: 5px;
    color: red;
}
.featuredSub{
    font-size: 15px;
    color: grey;
}